<template>
    <div class="confirmation__card">
        <div class="confirmation__inner">
            <div v-if="!fromNelsette" class="ordinary__user">
                <p style="font-weight: 500; margin-bottom: 16px">Потверждение заказа</p>
                <p class="confirmation__text" style="margin-bottom: 16px">
                    На номер телефон клиента был отправлен код, для подтверждения заказа попросите назвать его вам.
                </p>
                <div class="confirmation__code">
                    <input maxlength="1" type="text"  @keyup="handleClick('','one', 'two')" inputmode="tel" ref="one" >
                    <input maxlength="1" type="text"  @keyup="handleClick('one', 'two', 'three')" inputmode="tel" ref="two" >
                    <input maxlength="1" type="text"  @keyup="handleClick('two','three', 'four')" inputmode="tel" ref="three">
                    <input maxlength="1" type="text"  @keyup="handleClick('three', 'four', 'five')" inputmode="tel" ref="four" >
                    <input maxlength="1" type="text"  @keyup="handleClick('four', 'five', '')" inputmode="tel" ref="five" >
                </div>
                <div class="sendConfirmation" @click="sendOrder()" :class="{'enabled': code.length===5&&!clicked}">
                    Отправить
                </div>
                 <div class="sendConfirmation__again" @click="sendOrder()" v-if="clicked">
                    <p>Отправить код повторно ({{getTime}})</p>
                </div>
            </div>
            <div  v-else-if="fromNelsette" class="nelsette__user">
                <p style="margin-bottom: 16px">Ожидание потверждения заказа</p>
                <p>{{order.client.firstname}} {{order.client.lastname}} должен потвердить заказ зайдя в приложение</p>
            </div>
        </div>
    </div>    
</template>
<script>
import axios from 'axios';
import {mapGetters, mapState} from 'vuex'
export default {
    props: ['fromNelsette'],
    data(){
        return{
            code: '',
            clicked: false,
            timeOut: false,
            seconds: 300
        }
    },
    methods: {
        handleClick(prev, itself, next){
            if(this.$refs[itself].value.length){
                this.code+=this.$refs[itself].value;
                if(next!=''){
                    this.$refs[next].focus();
                }
                else{
                    this.$refs[itself].blur();
                }
            }
            else{
                this.code = this.code.substring(0, this.code.length-1);
                if(prev!=''){
                    this.$refs[prev].focus()
                }
            }
        },
        async sendOrder(){
            this.clicked = true;
            const countdown = setInterval(()=>{
                if(this.seconds>=1){
                    this.seconds-=1
                }
                else{
                    this.clicked = false;
                    this.seconds = 300;
                    clearInterval(countdown)
                }
            }, 1000)
            await axios.post(`${this.url}/crm/user/${window.$cookies.get('id')}/order/${this.order.id}/accept`, {
                client_id: this.order.client.id,
                code: this.code
            },
            {headers: {'Authorization': `Bearer ${window.$cookies.get('access_token')}`}}).then((res)=>{
                this.$store.dispatch('order/updateOrder', res.data.order);
                this.order.booking.forEach(b => b.status = 'activated');
                this.$store.dispatch('booking/setStatus', 'activated');
                clearInterval(countdown);
                this.clicked = false;
                this.seconds = 300;
            })
        },
    },
    computed: {
        ...mapGetters('order', ['getOrder']),
        ...mapGetters('booking', ['getBooking']),
        ...mapState('order', ['order']),
        getTime(){
            return `${Math.floor(this.seconds/60)}`.padStart(2,'0')+':'+`${this.seconds%60}`.padStart(2,'0')
        }
    }
}
</script>
<style lang="scss" scoped>
*{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    outline: none;
    border: none;
}
.confirmation__card{
    padding: 8px 16px;
    background: #FDFF97;
    margin: 16px 0px;
}
.confirmation__code{
    display: flex;
    input{
        width: 34px;
        height: 40px;
        background: #BEBEBE;
        margin-right: 36px;
        font-size: 24px;
        text-align: center;
    }
}
.sendConfirmation{
    padding: 5px 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 16px;
    width: 85px;
    pointer-events: none;
    background: #9D9D9D;
    color: #fff;
}
.enabled{
    background: #000;
    pointer-events: inherit;
}
</style>