<template>
    <div class="mini__card" @click.stop="goToBookingCard()" :class="{'fullpay': getRemainMoney<=0}">
        <div class="miniCard__inner">
            <div class="miniCard__top">
                <span class="order__date">{{capitalizeAndFormat(booking.date)}}</span>
                <span class="order__edit" v-show="orderValid&&booking.status!=='completed'" @click.stop="goToEdit()"><i class="fas fa-pen"></i></span>
            </div>
            <div class="miniCard__bottom">
                <div class="bottom__description">
                    <p>{{booking.start_time.substring(0,5)}} - {{booking.end_time.substring(0,5)}}</p>
                    <p>{{booking.playfield.pf_name}}</p>
                    <p v-show="orderValid">Остаток:  {{booking.debt}}₸</p>
                </div>
                <div class="bottom__payment" @click.stop="setBooking()" v-if="getRemainMoney>0&&orderValid">
                    Добавить оплату
                </div>
            </div>
        </div>
        <payment-popup v-if="openPopup" @addPayment="addPayment" @closePayment="closePayment" :remain="getRemainMoney">
            <template v-slot:title>
                <p>Добавьте оплату</p>
            </template>
        </payment-popup>
    </div>
</template>
<script>
import * as dayjs from 'dayjs'
import 'dayjs/locale/ru'
import {mapGetters, mapState} from 'vuex'
import PaymentPopup from './PaymentPopup.vue'
export default {
    props: ['booking'],
    components: {
        PaymentPopup
    },
    data(){
        return{
            orders: JSON.parse(localStorage.getItem('orderInfo')),
            openPopup: false
        }
    },
    methods: {
        capitalizeAndFormat(s){
            s = dayjs(s).locale('ru').format('dd, DD MMMM')
            return s.replace(s[0],s[0].toUpperCase())
        },
        setBooking(){
            this.openPopup = true;
            this.$store.dispatch('booking/setBooking', this.booking);
        },
        async addPayment(money){
            this.openPopup = false;
            await this.$store.dispatch('booking/setBookingPaid', {money: money, id:this.booking.id});
            window.location.reload()
        },
        closePayment(){
            this.openPopup = false;
        },
        goToBookingCard(){
                this.setBooking();
                this.$router.push({path:`/order/:${this.order.id}/booking/:${this.booking.id}`})
        
        },
        goToEdit(){
            this.$store.dispatch('booking/setBooking', this.booking);
            this.$router.push({path:`/order/:${this.booking.order_id}/booking/:${this.booking.id}/edit`})
        }
    },
    computed: {
        ...mapState('order', ['order']),
        ...mapGetters('booking', ['getRemainMoney']),
        orderValid(){
            return this.booking.status!=='pending'&&this.booking.status!=='canceled'
        },
        getRemainMoney(){
            return this.booking.price-this.booking.paid-this.booking.discount
        }
    }

}
</script>
<style lang="scss" scoped>
:not(i){
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
}
.mini__card{
    background: #fff;
    padding: 8px 8px 16px 8px;
    margin-bottom: 8px;
}
.miniCard__top{
    display: flex;
    justify-content: space-between;
    align-self: center;
    margin-bottom: 12px;;
}
.miniCard__bottom{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.order__date{
    line-height: 24px;
    font-weight: 500;
}
.bottom__description{
    p{
        font-size: 12px;
        line-height: 14px;
    }
}
.bottom__payment{
    padding: 4px 24px;
    background: rgba(0, 0, 0, 0.04);
    border: 0.3px solid rgba(0, 0, 0, 0.5); 
    cursor: pointer;
}
.fullpay{
    background: linear-gradient(0deg, rgba(79, 244, 20, 0.15), rgba(79, 244, 20, 0.15)), #FFFFFF;
}
</style>