<template>
    <div class="order__wrapper">
        <order-header>
            <template v-slot:orderHeader>
                <p class= "new__order">Новый заказ</p>
                <div class="close__order" @click="goBack()">
                    <i class="fa fa-times-circle" aria-hidden="true"></i>
                </div>
            </template>
            <template v-slot:headerBottom>
                <div class="header__bottom">
                    <p>Добавление клиента</p>
                </div>
            </template>
        </order-header> 
        <order-info-before :buttonNeeded = true />
        <div class="search__input">
            <label  class="phone__label" for="phone">Номер телефона</label><br>

            <input 
            @keypress.stop="isNumber($event)" 
            @keyup.stop="isValid()" 
            type="tel" 
            name="phone" 
            id="phone" 
            v-mask="'+7 ### ### ## ##'" 
            placeholder="Введите номер телефона" v-model="search"
            ref="telephone"
            >              
        </div>
        <div class = "continue__btn" @click="searchNumber()" :class="{'valid': isValid()}">Продолжить</div>
    </div>
</template>
<script>
import OrderInfoBefore from '../components/OrderInfoBefore.vue'
import OrderHeader from '../components/OrderHeader.vue'
import axios from 'axios'
import { mapState } from 'vuex'
export default {
    components: { OrderInfoBefore, OrderHeader},
    data(){
        return{
            search: '',
            discount: 0,
            client: {},
        }
    },
    methods: {
        async searchNumber(){
            await axios.post( `${this.url}/crm/user/${window.$cookies.get('id')}/client-interaction`, {phone: `${this.search.split(' ').join('')}`},
              {
                headers: {
                    'Authorization': `Bearer ${window.$cookies.get('access_token')}`
                }
            }).then(e=>this.$store.dispatch('order/setClient', e.data.client));
            this.$router.push({name: 'registerUser'})
        },
        goBack(){
            this.$store.dispatch('booking/resetState')
            this.$store.dispatch('order/resetState')
            this.$router.push({path: '/' })
        },
        isNumber: function(evt) {
            evt = (evt) ? evt : window.event;
            let charCode = (evt.which) ? evt.which : evt.keyCode;
            if (charCode > 31 && (charCode != 46 &&(charCode < 48 || charCode > 57))){
                return false
            }
                return true;
        },
        isValid(){
            return this.search.length&&!this.search.includes("_")
        }
    },
    computed: {...mapState('order', ['order'])}
}
</script>
<style lang="scss" scoped>
$font-stack: 'Roboto', sans-serif;
    .order__wrapper{
        height: 100vh;
        background: #E5E5E5;
    }
    label{
        font-family: $font-stack;
        font-size: 14px;
        }
        .search__input{
            padding: 16px 12px;
        }
        input {
            border: 1px solid #9D9D9D;
            width: 100%;
            padding: 7px 10px;
            height: 40px;
            font-family: $font-stack;
            font-size: 14px;
            margin-top: 4px;
    }
    .continue__btn{
        width: 97px;
        height: 30px;
        font-family: $font-stack;
        font-size: 14px;
        padding: 5px 7px;
        position: absolute;
        left: 16px;
        bottom: 21px;
        pointer-events: none;
        background: #9D9D9D;
        color: #000000;
    }
    .valid{
        background: #000000;
        color: #fff;
        pointer-events: initial;
    }
</style>