<template>
    <div class = "mainHeader">
        <div class="mainHeader__inner">
            <div class="mainHeader__top">
                <slot name="title" v-if="!filterType" />
                <div class="mainHeader__filters" v-if="filterType">
                    <div @click ='setFilter("oneDay")' class="filter__day" :class="active==='oneDay'?'active':''">
                        <p>День</p>
                    </div>
                    <div @click ='setFilter("allTime")' class="filter__allTime" :class="active==='allTime'?'active':''">
                        <p>Все время</p>
                    </div>
                </div>
                <div class="dropdown" v-if="options.length>1">
                    <DropDown  :options="options" :id="'facility'" :initialValue="initial" @selected="selected"/>
                </div>
            </div>
            <div class="mainHeader__bottom">
                <div class="number">
                    <input v-model="telephone" type="tel" id = "phone" name="phone"  title="+7" v-mask="'+7 ### ### ## ##'" placeholder="+7">
                </div>
                <div class="button">
                    Отправить по WhatsApp
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DropDown from './DropDown.vue'
export default {
    props: ['filterType'],
    components: {
        DropDown
    },
    data(){
        return{
            active: this.filterType, 
            options:[],
            initial: {},
            telephone: ''
        }
    },
    async created(){
        this.setFilter(this.filterType);
        this.options = JSON.parse(sessionStorage.getItem('facilities'))
        this.initial = this.options.find(e=>e.value==sessionStorage.getItem('active_facility'))||this.options[0];
        sessionStorage.setItem('active_facility', this.initial.value)

        
    },
    methods: {
        setFilter(filter){
            this.active = filter
            if(this.active==='oneDay'){
                this.$router.push({path: '/'})   
            }
            else if(this.active==='allTime'){
                this.$router.push({path: '/allTime', name: 'main'})
            }
        },
        selected(value, id){
            if(id==='facility'){
                this.$emit('changed', value.value);
            } 
        }
    }
}
</script>
<style lang="scss" scoped>
*{
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: #000000;
}
.mainHeader{
    position: fixed;
    z-index: 16;
    top: 0;
    left: 0;
    right: 0;
    background: #FFFFFF;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 2px rgba(0, 0, 0, 0.06), 0px 0px 1px rgba(0, 0, 0, 0.04);
    &__inner{
        padding: 12px 16px;
    }
    &__top{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .dropdown{
            min-width: 175px;
            height: 30px;
        }
        &>p{
            font-weight: 500;
            font-size: 20px;
            line-height: 32px;
        }
    }
    &__bottom{
        margin-top: 12px;
        display: flex;
        width: 100%;
        .number, .button{
            width: 50%;
            height: 30px;
            background: #FFFFFF;
            border: 1px solid #9D9D9D;
        }
        .number{
            input{
                outline: none;
                border: none;
                width: 100%;
                height: 100%;
                text-align: left;
                padding: 5px 8px;
            }
        }
        .button{
            display: flex;
            justify-content: center;
            align-items: center;
            border-left: none;
        }
    }
    &__filters{
        display: flex;
        justify-content: space-between;
        margin-right: 10px;
    }
}
.filter{
    &__day{
        width: 48px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 0.5px solid black;
        margin-right: 4px;
        cursor: pointer;
    }
    &__allTime{
        width: 83px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 0.5px solid black;
        cursor: pointer;
    }
}
.active{
    background: #A9A9A9;
}
</style>
