<template>
    <div class="main__content" :class="{'fullPay': fullPay}" @click="goToBooking()">
        <div class="content__inner">
            <div class="content_top">
                <div class="contentTop__inner">
                    <div class="content__left">
                        <div class="mini__description">
                            <b>{{info.start_time.substring(0,5)}}-{{info.end_time.substring(0,5)}}</b>, Площадка {{info.playfield_id}}
                        </div>
                        <div class="info__payload">
                            <div class="payload__type">
                                {{info.pay_type==="cash"?"Наличными":"По реквизитам"}}
                            </div>
                            <div class="payload__remain">
                                Остаток:  {{info.debt}}тг
                            </div>
                        </div>
                    </div>
                    <div class="content__right">
                        <ClientCardMini :client="info.client"/>
                    </div>
                </div>
            </div>
            <div v-if='!fullPay' class="content__bottom">
                    <div class="payload__button" @click.stop="openPopup=true">
                        Добавить оплату
                    </div>
            </div>
            <div class="payment">
                <payment-popup v-if="openPopup" @addPayment="addPayload" @closePayment="openPopup = false" :remain="info.debt" :hasMenu="true">
                <template v-slot:title>
                    <p>Добавьте оплату</p>
                </template>    
            </payment-popup> 
            </div>
        </div>
    </div>
</template>
<script>
import ClientCardMini from './ClientCardMini.vue'
import PaymentPopup from './PaymentPopup.vue'
export default {
    data(){
        return{
            openPopup:false
        }
    },
    props: ['info'],
    components: {
        ClientCardMini, PaymentPopup
    },
    computed: {
        fullPay: function(){
            return  this.info.debt<=0;
        },
        getRemainMoney: function(){
            return this.info.price-(this.info.paid+this.info.booking_discount)
        }
    },
    methods: {
        async addPayload(money){
            await this.$store.dispatch('booking/setBookingPaid', {money: money, id:this.info.id});
            this.openPopup = false;
            window.location.reload()
        },
        goToBooking(){
            this.$store.dispatch('order/setOrder', this.info)
            this.$store.dispatch('booking/setBooking', this.info)
            this.$router.push({path:`/order/:${this.info.order_id}/booking/:${this.info.id}`})
        }
    }
}
</script>
<style lang="scss" scoped>
*{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
}
.main__content{
    background: #FFFFFF;
    margin: 16px;
}
.content_top{
    padding-left: 12px;
    padding-top: 12px;
    padding-bottom: 26px;
}
.contentTop__inner{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.payload__type, .payload__remain{
    font-size: 12px;
}
.client{
    &__photo{
    display: flex;
    justify-content: center;
    align-items: center;
        img{
            width: 50px;
            height: 50px;
            border-radius: 50%;
            }
    }
    &__info{
        margin: 8px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__name{
        margin-left: 8px;
        font-weight: 700;
    }
}
.fullPay{
    background: linear-gradient(0deg, rgba(79, 244, 20, 0.15), rgba(79, 244, 20, 0.15)), #FFFFFF;
}
.payload__button{
    width: 100%;
    background: rgba(0, 0, 0, 0.04);
    border: 0.3px solid rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}
</style>