<template>
<div class="clientCard">
    <div class="clientCard__inner">
        <div class="client__info">
            <div class="info__left">
                <div class="name__surname">{{client.firstname}} {{client.lastname}}</div>
                <div class="company">{{client.company}}</div>
                <div class="call"><a :href="`tel:${client.phone}`">Позвонить <span>{{client.phone}}</span></a></div>
                <div class="text"><a :href="`https://api.whatsapp.com/send?phone=${client.phone}`" target="_blank">Написать в <span>WhatsApp</span></a></div>
            </div>
            <div class="info__right">
                <div class="client__photo">
                    <img :src="`${this.url}/storage/profile_original_avatars/${client.avatar}`" alt="Photo" v-if="client.avatar!=='default_avatar.png'">
                    <i  v-else class="fas fa-user-circle"></i>
                </div>
            </div>
        </div>
        <div class="order__info">
            <p class="order__amount">Общее количество заказов:{{order_info.orders_count}}</p>
            <div class="order__current">
                <p>Текущие: {{order_info.orders_activated}}</p> 
                <div class="btn"><i class="fas fa-chevron-right"></i></div>
            </div>
            <div class="order__finished">
                <p>Завершеннные: {{order_info.orders_completed}}</p> 
                <div class="btn"><i class="fas fa-chevron-right"></i></div>
            </div>
            <div class="order__canceled">
                <p>Отмененные: {{order_info.orders_canceled}}</p>
                <div class="btn"><i class="fas fa-chevron-right"></i></div> 
            </div>
        </div>
        <div class="payload__info">
            <p style="margin: 16px 0px;font-weight:500">Оплата</p>
            <div class="all__paid">
                <div class="key">Всего оплачено:</div>
                <div class="value">{{order_info.paid}} ₸</div>
            </div>
            <div class="remaining__current">
                <div class="key">Остаток по текущим заказам:</div>
                <div class="value">{{order_info.residue}} ₸</div>
            </div>
            <div class="debt">
                <div class="key">Долги:</div>
                <div class="value">{{order_info.debt}} ₸</div>
            </div>
        </div> 
    </div>    
</div>    
</template>
<script>
import axios from 'axios'
export default {
    props:['client', 'clicked'],
    data(){
        return{
            order_info: {}, 
            start: false,
            click: this.clicked
        }
    },
    watch: {
        clicked: async function(){
            if(this.clicked){
                this.start =  true;
                await axios.post(`${this.url}/crm/user/${window.$cookies.get('id')}/orders-cli`, {
                    client_id: this.client.id
                },
                {
                headers: {'Authorization': `Bearer ${window.$cookies.get('access_token')}`}
            }).then((e)=>{
                this.order_info = e.data.data;
                this.start = false;}
            ).catch((e)=>console.log(e));
            }
        }
    }
}
</script>
<style lang="scss" scoped>
:not(i){
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
}
.clientCard{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    padding: 16px;
    z-index: 20;
}
.client{
    &__info{
        display: flex;
        margin-bottom: 16px;
    }
    &__photo{
        width: 64px;
        height: 64px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        img, i {
            width: 100%;
            height: 100%;
            font-size: 64px;
            border-radius: 50%;
            border: 1px solid rgba(0, 0, 0, 0.2);
        }
    }
}
.info{
    &__left{
        width: 75%;
    }
    &__right{
        width: 25%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
}
.name__surname{
    font-weight: 500;
    font-size: 20px;
    line-height: 32px;
}
.call{
    margin-top: 8px;
    margin-bottom: 4px;
    a{
        color: #000;
        text-decoration: none;
    }
    span{
        text-decoration: underline;
    }
}
.text{
    a{
        color: #000;
        text-decoration: none;
    }
    span{
        text-decoration: underline;
    }
}
.order{
    &__amount{
        font-weight: 500;
        margin-bottom: 16px;
    }
    &__current, &__finished, &__canceled{
        margin-bottom: 8px;
        background: rgba(0, 0, 0, 0.04);
        padding: 8px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
.btn{
    width: 24px;
    height: 24px;
    background: #FFFFFF;
     box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 2px rgba(0, 0, 0, 0.06), 0px 0px 1px rgba(0, 0, 0, 0.04);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.all__paid, .remaining__current, .debt{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.debt>.value{
    color: #FF4D4D;
}
</style>