<template>
    <div class="facility__payment" @click="clicked = true">
        <div class="backdrop" v-show="clicked" @click.stop="closePopup"></div>
        <div class="flex">
            <div class="flex__key">Условия оплаты</div>
            <i class="fas fa-chevron-right"></i>
        </div>
        <div class="value">Предоплата: {{prepaid__amount}} ₸</div>
        <edit-popup v-show="clicked" @close="closePopup">
            <template v-slot:title>
                <p class="facility__title">Условия предоплаты</p>
            </template>
            <template v-slot:content>
                <div class="content">
                    <div class="content__inner">
                        <p class="payment__title">Сумма предоплаты*</p>
                        <input type="text" name="prepaid" id="prepaid" placeholder="0 ₸" v-model="prepaid__amount" @keypress.stop="isNumber($event)" inputmode="tel">
                        <div class="dropdown__cancel">
                            <p class="cancel__title">Время отмены брони</p>
                            <div class="dropdown">
                                <select ref="cancel_time">
                                    <option value="0">В любое время</option>
                                    <option value="3600">За 1 час</option>
                                    <option value="7200">За 2 часа</option>
                                    <option value="14400">За 4 часа</option>
                                    <option value="21600">За 6 часов</option>
                                    <option value="28800">За 8 часов</option>
                                    <option value="36000">За 10 часов</option>
                                    <option value="43200">За 12 часов</option>
                                    <option value="86400">За 24 часа</option>
                                    <option value="172800">За 48 часов</option>
                                </select>
                            </div>
                        </div>
                    </div>
                </div>
            </template>
            <template v-slot:button>
                <div class="save__btn">
                    <div class="btn" :class="{'active':prepaid__amount.trim().length}" @click.stop.prevent="setPrepaidCondition()">Применить</div>
                </div>
        </template>
        </edit-popup>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import EditPopup from './EditPopup.vue'
import axios from 'axios';
export default {
    computed: {...mapState('facility', ['facility'])},
    components: {EditPopup},
    data(){
        return{
            clicked: false,
            prepaid__amount: '',
        }
    },
    mounted(){
        this.setValues();
    },
    methods: {
        closePopup(){
            this.clicked = false;
            this.setValues();
        },
        async setPrepaidCondition(){
            await axios.post(`${this.url}/crm/user/${window.$cookies.get('id')}/facility/${this.facility.id}/update-data`, {conditions: {prepaid: this.prepaid__amount, cancel_time: this.$refs['cancel_time'].value},
            }, 
            {
                headers: {'Authorization': `Bearer ${window.$cookies.get('access_token')}`
                }
            })
            .then(e=>this.$store.dispatch('facility/setFacilityPaymentCondition', e.data.facility.conditions));
            this.clicked = false;
        },
        isNumber(evt){
            evt = (evt) ? evt : window.event;
            let charCode = (evt.which) ? evt.which : evt.keyCode;
            if (charCode > 31 && (charCode < 48 || charCode > 57)){
                evt.preventDefault();
            }
            else {
                return true;
            }   
        },
        setValues(){ 
            this.$refs['cancel_time'].value = this.facility.conditions.cancel_time||'0';
            this.prepaid__amount = this.facility.conditions?.prepaid||'0'; 
        }
    }
}
</script>
<style lang="scss" scoped>
:not(i){
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
}
.facility{
    &__payment{
        padding: 16px;
        background: #fff;
        box-shadow: inset 0px -1px 0px rgba(0, 0, 0, 0.1);
        .value{ 
            color: rgba(0, 0, 0, 0.6);
        }
    }
    &__title{
        font-weight: 500;
        font-size: 20px;
        line-height: 32px;
        margin-bottom: 20px;
    }
}
.flex{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 6px;
    &__key{
        font-size: 16px;
    }
    i{
        color: rgba(0, 0, 0, 0.6);
    }
}
.content{
    &__inner{
        #prepaid{
            width: 100%;
            height: 36px;
            padding: 8px;
            margin: 8px 0px 10px;
        }
        .dropdown__cancel{
            display: flex;
            justify-content: space-between;
            select{
                border: none;
                outline: none;
            }
        }
    }
}
.save__btn{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
    .btn{
        padding: 8px 0px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        background: rgba(0, 0, 0, 0.2);
        pointer-events: none;
        width: 100%;
        max-width: 343px;
    }
    .active{
        pointer-events: initial;
        background: #000;
    }
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 10;
}
</style>