import axios from 'axios'
const adminModule = {
    namespaced: true,
    state: {
        user: {
            id: null,
            access_token: '',
            isAuthenticated: false
        }
    },
    mutations: {
        setIsAuthenticated(state, value){
            state.user.isAuthenticated = value;
        }
    },
    actions: {
        // eslint-disable-next-line no-empty-pattern
        async loginUser({} , user){
            await axios.post('${this.url}/crm/user/login', {
                username: user.tel,
                password: user.password
            }).then(res=>{
                if(res.status===200){
                    window.$cookies.set('access_token', res.data.token.access_token, '30d');
                    window.$cookies.set('id', res.data.user.id, '30d');
                }
            }).catch(e=>e)
        }
    }
}
export default adminModule;