<template>
    <div class="clients__wrapper">
        <MainHeader @changed="changed">
            <template v-slot:title>
                <p class="title">Клиенты</p>
            </template>
        </MainHeader>
        <p class="client__amount">
            Клиентов: {{clients.length}}
        </p>
        <div class="clientCards">
            <Client v-for="client in clients" :key="client.id" :client="client"/>
        </div>
        <bottom-menu active="clients"/>
    </div>
</template>
<script>
import Client from '../components/Client.vue'
import BottomMenu from '../components/BottomMenu.vue'
import MainHeader from '../components/MainHeader.vue'
import axios from 'axios'
import {mapState} from 'vuex'
export default {
    components: {Client, BottomMenu,MainHeader},
    data(){
        return{
            clients: []
        }
    },
    watch: {
        'facility.id': async function(){
            await this.getClients()
            }
    },
    async mounted(){
        await this.getClients();
    },
    methods:{
        changed(id){
            this.start = true;
            this.$store.dispatch('facility/resetFacility', parseInt(id)).then(()=>this.start = false);
            sessionStorage.setItem('active_facility', parseInt(id))
        },
        async getClients(){
            axios.get(`${this.url}/crm/user/${window.$cookies.get('id')}/facility/${sessionStorage.getItem('active_facility')}/users`, {
            headers: {'Authorization': `Bearer ${window.$cookies.get('access_token')}`}
            }).then(e=>this.clients = e.data.clients)
        }
    },
    computed: {
        ...mapState('facility', ['facility'])
    }
}
</script>
<style lang="scss" scoped>
:not(i){
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
}
.clients__wrapper{
    min-height: 100vh;
    background: #E5E5E5;
    .client__amount{
        padding: 16px;
        margin-top: 98px;
    }
    .client__title{
        font-weight: 500;
        font-size: 20px;
        line-height: 32px;
    }
    .clientCards{
        margin-bottom: 49px;
    }
}
</style>