<template>
    <div class="single__order">
        <div v-if="show||showConditions" class="backdrop" @click.stop="show = false; showConditions=false"></div>
        <ConfirmHeader>
            <template v-slot:main>
                <p class="order__id">Заказ №{{order.id}}</p>
            </template>
            <template v-slot:status>
                <p class="order__status">{{getOrderStatus}}</p>
            </template>
        </ConfirmHeader>
        <confirmation-card v-if="order.status==='pending'" :fromNelsette="!order.client.is_temporary"/>
        <OrderInfo :buttonNeeded="false"></OrderInfo>
        <div class="confirm__popup" v-show="show">
            <div class="popup__inner">
                <i class="fas fa-times" @click="show=false"></i>
                <p class="confirm__text">Вы уверены, что хотите отменить все?</p>
            <div class="confirm__buttons">
                <div class="button__yes" @click="cancelOrder(true)">Да</div>
                <div class="button__no" @click="cancelOrder(false)">Нет</div>
            </div>
            </div>
        </div>
        <div class="booking__status">
            <div class="bookingStatus__inner">
                <p>Брони</p>
                <div class="statuses">
                    <div class="upcoming" :class="{'active':active==='upcoming'}" @click="active='upcoming'">
                        Предстоящие: {{activeCount}}
                        <div class="line"></div>
                    </div>
                    <div class="finished" :class="{'active':active==='finished'}" @click="active='finished'">
                        Завершенные: {{finishedCount}}
                        <div class="line"></div>
                    </div>
                    <div class="canceled" :class="{'active':active==='canceled'}" @click="active='canceled'">
                        Отмененные: {{canceledCount}}
                        <div class="line"></div>
                    </div>
                </div>
            </div>
        </div>
        <div class="cancel__order" v-show="!IsArchive&&active==='upcoming'&&containsUnpaid">
            <p class="cancel__order_btn" @click="show=true" :class="{'canCancel':order.deposit===0&&order.status==='activated'}">Отменить брони</p>
            <p class="cancel__order_conditions" @click="showConditions = true">Условия отмены</p>
        </div>
        <div class="condition__popup" v-show="showConditions">
            <div class="condition__popup_inner">
                <p class="condition__title">Отмена броней</p>
                <p class="condition one">Нажатием кнопки "Отменить брони", вы отменяете все брони за которые не была внесена предоплата.</p>
                <div class="condition two">Отмена броней невозможна если был внесён залог.</div>
                <div class="close__popup" @click="showConditions=false">Закрыть</div>
            </div>
        </div>
        <div class="miniCards">
            <MiniOrderCard v-for="b,index in filteredBookings" :booking="b" :key="index"/> 
        </div>
        <div class="noBookings" v-show="filteredBookings.length===0">
            <p v-show="active==='upcoming'">Нет предстоящих заказов</p>
            <p v-show="active==='finished'">Нет завершенных заказов</p>
            <p v-show="active==='canceled'">Нет отмененных заказов</p>
        </div>
    </div>
</template>
<script>
import {mapGetters, mapState} from 'vuex'
import ConfirmHeader from '../components/ConfirmHeader.vue'
import OrderInfo from '../components/OrderInfo.vue'
import MiniOrderCard from '../components/MiniOrderCard.vue'
import ConfirmationCard from '../components/ConfirmationCard.vue'
import * as dayjs from 'dayjs'
import 'dayjs/locale/ru'
import axios from 'axios'
export default {
    props: ['id'],
    data(){
        return{
            show: false,
            active: 'upcoming',
            remainForBooking: 0,
            showConditions: false,
            bookings:[]
        }
    },
    async mounted(){
        await this.getSingleOrder();
        this.getBookings(this.active);
    },
  components: { ConfirmHeader, OrderInfo, MiniOrderCard,ConfirmationCard},
  computed: {
      ...mapState('order', ['order']),
      ...mapGetters('order', ['getAllPaid', 'getRemainMoney']),
        IsArchive(){
            return this.order.status==='canceled'||this.order.status==='completed';
        },
        containsUnpaid(){
            return this.filteredBookings.filter(e=>e.paid===0).length!==0
        },
        getOrderStatus(){
            switch(this.order.status){
                case 'pending': 
                    return 'В ожидании';
                case 'activated':
                    return 'Потвержден';
                case 'canceled': 
                    return 'Отменен';
                case 'completed': 
                    return 'Завершен';
                default:
                    return true;
            }
        },
        activeCount(){
            return this.bookings.filter(b=>b.status==='activated'||b.status==='pending').length;
        },
        canceledCount(){
            return this.bookings.filter(b=>b.status==='canceled').length;
        },
        finishedCount(){
            return this.bookings.filter(b=>b.status==='completed').length;
        },
        filteredBookings(){
            switch(this.active){
                case 'upcoming':
                    return this.bookings.filter(e=>e.status==='pending'||e.status==='activated');
                case 'finished':
                    return this.bookings.filter(e=>e.status==='completed');
                case 'canceled':
                    return  this.bookings.filter(e=>e.status==='canceled');
                default:
                    return [];
          }
        }
  },
  methods: {
      getBookings(status){
          this.active = status
      },
        isMoreThanSevenDays(date){
            return dayjs(date).diff(dayjs())>7
        },
        async cancelOrder(bool){
            if(bool){
                await axios.post(`${this.url}/crm/user/${window.$cookies.get('id')}/order/${this.order.id}/cancel-reservations`, {}, {
                headers: {'Authorization': `Bearer ${window.$cookies.get('access_token')}`}}).then(async ()=> {
                    this.active = "canceled";
                    await this.getSingleOrder();
                    })
            }
            this.show = false;
        },
        async getSingleOrder(){
            await axios.get(`${this.url}/crm/user/${window.$cookies.get('id')}/order/${this.id}`, {
                headers: {'Authorization': `Bearer ${window.$cookies.get('access_token')}`}
            }).then(res=>{
                this.$store.dispatch('order/setOrder', res.data.order);
                this.$store.dispatch('booking/setBooking', res.data.order.booking[0]);
                this.bookings = [...this.order.booking];
            });
        }
  }
}
</script>
<style lang="scss" scoped>
:not(i){
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
}
.single__order{
    background: #E5E5E5;
    min-height: 100vh;
}
.cancel__order{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
    p{
        font-size: 12px;
        line-height: 24px;
    }
    &_btn{
        color: rgba(0, 0, 0, 0.38);
        pointer-events: none;
    }
    &_conditions{
        padding: 4px;
        background: #fff;
    }
    .canCancel{
        color: #0066FF;
        pointer-events: initial;
    }
}

.condition{
    &__popup{
        padding: 24px 16px;
        background: #fff;
        z-index: 10;
        position: fixed;
        bottom: 0;
        .condition{
            margin-bottom: 16px;
        }
        .close__popup{
            display: flex;
            justify-content: center;
            align-items: center;
            font-weight: 500;
            font-size: 13px;
            line-height: 16px;
            text-align: center;
            letter-spacing: 1.25px;
            text-transform: uppercase;
            color: #03A9F4;
        }
    }
    &__title{
        font-weight: 500;
        font-size: 20px;
        line-height: 32px;
        color: rgba(0, 0, 0, 0.87);
        margin-bottom: 16px;
    }

}
.confirm__popup{
    width: 80%;
    height: 50%;
    max-width: 500px;
    max-height: 230px;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    z-index: 11;
    padding: 20px;
    .popup__inner{
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        position: relative;
        i{
            position: absolute;
            top: 0;
            right: 0;
        }
        .confirm__buttons{
            display: flex;
            width: 100%;
            .button__yes, .button__no{
                width: 50%;
                height: 30px;
                display: flex;
                justify-content: center;
                align-items: center;
                color: white;
            }
            .button__no{
                background: #50CB93;
            }
            .button__yes{
                background: #BD1616;
            }
    }
}
}
.booking__status{
    height: 88px;
    background: #fff;
    margin: 16px 0px;
    font-size: 12px;
    line-height: 14px;
    padding: 16px 16px 0 16px;
    p{
        font-weight: 500;
        font-size: 12px;
        line-height: 24px;
    }
    .statuses{
        height: 48px;
        width: 100%;
        display: flex;
        .upcoming, .canceled, .finished{
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            font-size: 12px;
            width: 33.3%;
        }
        .active>.line{
                position: absolute;
                bottom: 0;
                width: 100%;
                height: 2px;
                background: #000;
            }
            .active{
                font-weight: 500;

            }
    }
}
.miniCards, .noBookings{
    padding: 16px 16px 8px 16px;
}
.noBookings{
    display: flex;
    height: 300px;
    justify-content: center;
    align-items: center;
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 10;
}
</style>