<template>
    <div class="timetable__cell" :class="{'reserved': value.isReserved}"
        @click.stop="handleClick()"
    >
            {{value.value}}
    </div>
</template>
<script>
export default {
    props: ['field', 'index', 'value'],
    methods: {
        handleClick(){
            if(!this.value.isReserved){
                this.$emit('makeOrder');
            }
            else{
                console.log(this.value.booking_id);
                this.$emit('showInfoAboutOrder', this.value.booking_id)
            }
        }
    }
}
</script>
<style lang="scss" scoped>
    .timetable__cell{
        width: 49px;
        height: 100%;
        margin-left: 1px;
        background-color: #fff;
        font-family: 'Roboto', sans-serif;
        font-size: 12px;
        line-height: 20px;
        color: rgba(0,0,0,0.5);
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
    }
    .reserved{
        background:  #A8A8A8!important;
    }
</style>