<template>
    <div class="order__view">
        <MainHeader @changed="changed">
            <template v-slot:title>
            <p class="title">Заказы</p>
            </template>
        </MainHeader>
        <div class="orderView__header">
            <div class="header__inner">
                <div class="order__status">
                    <div class="order__waiting" :class="active==='В ожидании'?'active':''" @click="makeActive('В ожидании')" v-if="getWaiting.length">
                        <p>В ожидании</p>
                        <p class="amount">{{getWaiting.length}}</p>
                    </div>
                    <div class="order__confirmed" :class="active==='Активные'?'active':''" @click="makeActive('Активные')">
                        <p>Активные</p>
                        <p class="amount">{{getConfirmed.length}}</p>
                    </div>
                    <div class="order__archive" :class="active==='Архив'?'active':''" @click="makeActive('Архив')">
                        <p>Архив</p>
                        <p class="amount">{{getArchive.length}}</p>
                    </div>
                </div>
            </div>
        </div>
        <OrderCardWrapper :status="active" :filteredOrders="filteredOrders"/>
        <BottomMenu active="orders" :waiting="getWaiting.length"/>
    </div>
</template>
<script>
import BottomMenu from '../components/BottomMenu.vue'
import OrderCardWrapper from '../components/OrderCardWrapper.vue'
import MainHeader from '../components/MainHeader.vue'
import * as dayjs from 'dayjs'
import 'dayjs/locale/ru'
import axios from 'axios';
import { mapState } from 'vuex'
export default {
    components: {BottomMenu, OrderCardWrapper,MainHeader},
    data(){
        return{
            active: "В ожидании",
            orders: [],
            filteredOrders: [],
            today: dayjs().locale('ru'),
            start: true
        }
    }, 
    watch: {
        'facility.id': async function(){
              await this.getOrders();
              this.active = "В ожидании";
              this.filteredOrders = this.getWaiting;
        }
    },
    async created(){
        await this.getOrders();
        this.filteredOrders = this.getWaiting;
    },
    methods: {
        async getOrders(){
            await axios.get(`${this.url}/crm/user/${window.$cookies.get('id')}/facility/${sessionStorage.getItem('active_facility')}/orders-all`, {
            headers: {
                    'Authorization': `Bearer ${window.$cookies.get('access_token')}`
                }
        }).then(res=>{
            this.start = false;
            this.orders = res.data.orders;
        }); 
        },
        makeActive(status){
            this.active = status;
            switch(this.active){
                case "Активные":
                    this.filteredOrders = this.getConfirmed;
                    break;
                case "В ожидании":
                    this.filteredOrders = this.getWaiting;
                    break;
                case "Архив":
                    this.filteredOrders = this.getArchive;
                    break;
            }
        },
        changed(id){
            this.start = true;
            this.$store.dispatch('facility/resetFacility', parseInt(id)).then(()=>this.start = false);
            sessionStorage.setItem('active_facility', parseInt(id))
        }
    },
    computed:{
        ...mapState('facility', ['facility']), 
        getConfirmed(){
            return this.orders.filter(e=>e.status==='activated')
        },
        getWaiting(){
            return this.orders.filter(e=>e.status==='pending')
        },
        getArchive(){
            return this.orders.filter(e=>e.status==='canceled'||e.status==='completed');
        }
    }
}
</script>
<style lang="scss" scoped>
p{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 32px;
}
.order__view{
    min-height: 100vh;
    background: #E5E5E5;
}
.orderView__header{
    margin-bottom: 16px;
    background: #E5E5E5;
    margin-top: 98px;
    .header__title{
        display: flex;
        align-items: center;
        height: 62px;
        background: #FFFFFF;
        padding-left: 8px;
        padding-top: 10px;
        padding-bottom: 10px;
    }
    .order{
    &__title{
        font-weight: 500;
        font-size: 20px;
        margin-left: 12px;
    }
    &__status{
        display: flex;
        width: 100%;
        height: 30px;
        margin-top: 16px;
    }
    &__confirmed, &__waiting, &__archive{
        padding: 5px 7px;
        flex: 1 1 auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-left: 1px;
        background: #fff;
        .amount{
            width: 24px;
            height: 20px;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            background: rgba(0, 0, 0, 0.04);
            margin-left: 8px;
        }
    }
    }
}
.active{
    background: #A9A9A9!important;
    .amount{
        background: #FBE83C!important;
    }
}

</style>